  padding: 4rem 0rem;
  border-radius: 0 2rem 2rem 0;
  margin: 1rem 0;
  position: relative;
}
.contenedor .sidebar .sidebar_image {
  max-width: 100%;
}
.contenedor .sidebar .sidebar_image img {
  border-radius: 50%;
}
.contenedor .sidebar .menu_links {
  width: 100%;
  margin: 0;
  padding: 0 2rem;
  list-style-type: none;
}
.contenedor .sidebar .menu_item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 5rem;
  margin-top: 1.5rem;
  padding: 0 2rem;
  border-radius: 1.5rem;
  background-color: #4557B5;
  list-style: none;
}
.contenedor .sidebar .menu_item.active {
  transition: all 0.5s;
  background-color: #fff;
}
.contenedor .sidebar .menu_item.active .menu_link,
.contenedor .sidebar .menu_item.active .menu_icon {
  cursor: pointer;
}
.contenedor .sidebar .menu_item.active .menu_link {
  color: #4557B5;
}
.contenedor .sidebar .menu_item.active .menu_icon {
  filter: invert(33%) sepia(28%) saturate(2123%) hue-rotate(199deg) brightness(96%) contrast(85%);
}
.contenedor .sidebar .menu_item .menu_link {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: 100%;
  font-weight: 700;
  color: #F2F5FC;
}
.contenedor .sidebar .menu_item .menu_icon {
  width: 100%;
  max-width: 2rem;
  filter: invert(94%) sepia(7%) saturate(1393%) hue-rotate(189deg) brightness(105%) contrast(98%);
}
.contenedor .sidebar_footer {
  width: 100%;
}
.contenedor .sidebar_footer .user {
  display: flex;
  justify-content: center;
  align-items: center;
  column-gap: 2rem;
}
.contenedor .sidebar_footer .user button {
  all: unset;
}
.contenedor .sidebar_footer .user .user_logout {
